import React from 'react';
import './FaceRecognition.css';
const FaceRecognition = ({imageUrl, box}) => {
			let page;
			let div = [];
			const pushBoudingBox = () => {
				for (let coordinates in box) {
					div.push(
		  <div className='bounding-box' 
		  style={{top: box[coordinates].topRow, left: box[coordinates].leftCol, bottom: box[coordinates].bottomRow, right: box[coordinates].rightCol}}>
		  </div>
						)
				}
			}
			pushBoudingBox();

	page = div.map(face => face)
return (
<div className='center ma'>
<div className='absolute mt2'>
	<img id='inputimage' src={imageUrl}  width='500px' height='auto'/>
 {page}
</div>
</div>	
	);
}

export default FaceRecognition;


