import React, { Component } from 'react';
import Navigation from './components/Navigation/Navigation';
import Logo from './components/Logo/Logo';
import Rank from './components/Rank/Rank';
import Signin from './components/Signin/Signin';
import Register from './components/Register/Register';
import ImageLinkForm from './components/ImageLinkForm/ImageLinkForm';
import Particles from 'react-particles-js';
import FaceRecognition from './components/FaceRecognition/FaceRecognition';
import './App.css';
import 'tachyons';
const http = require("http");
setInterval(function() {
    http.get("https://face-detecting.herokuapp.com/");
}, 300000); // every 5 minutes (300000)
const particlesOptions = {
particles: {
            number: {
              value: 30, 
              density: {
                enable: true,
                value_area: 800
              }
            }
            }
            }


const initialState = {
      input:'',
      imageUrl:'',
      box: '',
      route: 'signin',
      isSignedIn: false,
      user: {
            id: '',
            name: '',
            email: '',
            password: '',
            entries: 0,
            joined: ''
      }
    }

class App extends Component {
  constructor() {
    super();
    this.state = initialState;
  }

 

   onInputChange = (event) => {
    this.setState({input:event.target.value});
  }

   calculateBoundingBox = (data) => {
    const getImage = document.getElementById('inputimage');
    const height = Number(getImage.height);
    const width = Number(getImage.width);
 // For each face extract the bounding box
    const clarifaiFace = data.outputs[0].data.regions.map(regions => regions.region_info.bounding_box)
    console.log(clarifaiFace)
    //   const clarifaiFace = data.outputs[0].data.regions[0].region_info.bounding_box;
    console.log(height, width);
    // For each bounding box calculate coordinates
    const coordinates =  clarifaiFace.map(faceCoordinates => 
    ({
          topRow : height * faceCoordinates.top_row,
          bottomRow : height - faceCoordinates.bottom_row * height,
          leftCol : width * faceCoordinates.left_col,
          rightCol : width - faceCoordinates.right_col * width
    }))
    console.log(coordinates)
  return  coordinates;

}

  displayBoundingBox(box) {
    this.setState({box: box});
    console.log('box:');
    console.log(this.state.box);
  }

  onButtonSubmit = () => {
    //https://salty-coast-48303.herokuapp.com/
    this.setState({imageUrl:this.state.input})
        fetch('https://salty-coast-48303.herokuapp.com/imageurl', {
            method: 'post',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({
              input: this.state.input
            })
          })          
        .then(response => response.json())
.then(response =>   {
  console.log(response)
      if (response) {
                  fetch('https://salty-coast-48303.herokuapp.com/image', {
            method: 'put',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({
              id: this.state.user.id
            }) 
          })
            .then(response => response.json())
            .then(count => {
              this.setState(Object.assign(this.state.user, { entries: count}))
            })
                        this.displayBoundingBox(this.calculateBoundingBox(response))

      }
    })
    .catch(err => console.log(err));
  }

  onRouteChange = (route) => {
    if (route === 'signout') {
      this.setState(initialState);
    } else if (route === 'home') {
          this.setState({isSignedIn: true});
    }
    this.setState({route: route})
  }

  loadUser = (data) => {
    this.setState({
      user: {
            id: data.id,
            name: data.name,
            email: data.email,
            entries: data.entries,
            joined: data.joined
            }
    })
  }

  render() {
    return (
      <div className="App">
            <Particles className="particles"
              params={particlesOptions}
            />
            <Navigation onRouteChange={this.onRouteChange} isSignedIn={this.state.isSignedIn}/>

        {this.state.route === 'home' ? 
         <div>
          <Rank name={this.state.user.name} entries={this.state.user.entries}/>  
          <ImageLinkForm onInputChange={this.onInputChange} onButtonSubmit={this.onButtonSubmit}/>
          <FaceRecognition box={this.state.box} imageUrl={this.state.imageUrl}/>
         </div>
         : (
          (this.state.route === 'signin') ? 
          <Signin loadUser={this.loadUser}
 onRouteChange={this.onRouteChange} />
          : <Register loadUser = {this.loadUser} onRouteChange={this.onRouteChange}/>
)
       }

      </div>
    );
  }
}

export default App;



