import React from 'react';
import './ImageLinkForm.css';
const ImageLinkForm = ({onInputChange, onButtonSubmit}) => {
	return (
<div>
	<p className="pa3 f3">{'Copy a link of an image and paste it in the text field and hit detect button!'}</p>
	 <div className="center">
	 <div className="ImageLinkForm center pa4 br3 shadow-5">
	 	<input type="text" className="f4 pa2 w-70 center" onChange = {onInputChange}/>
	 	<button className="w-30 center grow f4 link ph3 pv2  dib black bg-light-grey"
	 	onClick={onButtonSubmit}>Detect</button>
	 	</div>
	 </div> 
</div>
	);
}
export default ImageLinkForm;


