import React from 'react';
class Signin extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      email: '',
      password: '',
      errorMessage: ''
    }
  }

  onEmailChange = (event) => {
    this.setState({email: event.target.value})
  }

 onPasswordChange = (event) => {
    this.setState({password: event.target.value})
  }

  onSubmitSignin = () => {
    //https://salty-coast-48303.herokuapp.com/signin
    fetch('https://salty-coast-48303.herokuapp.com/signin', {
      method: 'post',
      headers: {'Content-Type':'application/json'},
      body: JSON.stringify({
        email: this.state.email,
        password: this.state.password
      })
    })
    .then(response => response.json())
    .then(user => {
      if (user.id) {
            this.props.loadUser(user);
            this.props.onRouteChange('home')
      } else (
          this.setState({errorMessage: user})
        )
    })
  }

  render() {
   const {onRouteChange} = this.props;
  return (
  <div>
  <article className="br3 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l shadow-5 mw6 center">
    <main className="pa4 black-80">
  <div className="measure">
    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
      <legend className="f1 fw6 ph0 mh0">Sign In</legend>
      <div className="mt3">
        <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
        <input className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
        type="email" 
        onChange = {this.onEmailChange}
        name="email-address"  
        id="email-address"
        />
      </div>
      <div className="mv3">
        <label 
        className="db fw6 lh-copy f6" 
        htmlFor="password">Password
        </label>
        <input 
        className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
        type="password" 
        name="password"
        onChange = {this.onPasswordChange}  
        id="password"/>
      </div>
    </fieldset>
    <div>
      <p> {this.state.errorMessage} </p>
    </div>
    <div className="">
      <input className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" 
      type="submit" 
      value="Sign in"
      onClick={this.onSubmitSignin} 
      loadUser={this.props.loadUser}
/>
    </div>
    <div className="lh-copy mt3">
      <p onClick={onRouteChange} className="f6 link dim black db pointer">Register</p>
    </div>
  </div>
</main>  
</article>
  </div>
    );
  }
}
//
export default Signin;



